// import css non compile
@import "../scss-non-compile/breakpoint";
@import "../scss-non-compile/var";

@-webkit-keyframes "animate-loading" {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes "animate-loading" {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*--------------------------------------------------------------
# Credits
--------------------------------------------------------------*/
body {
  font-family: "Open Sans", sans-serif;
  background-color: #040404;
  color: #fff;
  position: relative;
  background: transparent;
  &::before {
    content: "";
    position: fixed;
    background: #040404 url("/images/bg.jpeg") top right no-repeat;
    background-size: cover;
    left: 0;
    right: 0;
    top: 0;
    height: 100vh;
    z-index: -1;
  }
}
a {
  color: #18d26e;
  text-decoration: none;
  &:hover {
    color: #35e888;
    text-decoration: none;
  }
}
h1 {
  font-family: "Raleway", sans-serif;
}
h2 {
  font-family: "Raleway", sans-serif;
}
h3 {
  font-family: "Raleway", sans-serif;
}
h4 {
  font-family: "Raleway", sans-serif;
}
h5 {
  font-family: "Raleway", sans-serif;
}
h6 {
  font-family: "Raleway", sans-serif;
}
#header {
  transition: ease-in-out 0.3s;
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  z-index: 997;
  overflow-y: auto;
  * {
    transition: ease-in-out 0.3s;
  }
  h1 {
    font-size: 48px;
    margin: 0;
    padding: 0;
    line-height: 1;
    font-weight: 700;
    font-family: "Poppins", sans-serif;
    a {
      color: #fff;
      line-height: 1;
      display: inline-block;
      &:hover {
        color: #fff;
        line-height: 1;
        display: inline-block;
      }
    }
  }
  h2 {
    font-size: 24px;
    margin-top: 20px;
    color: rgba(255, 255, 255, 0.8);
    span {
      color: #fff;
      border-bottom: 2px solid #18d26e;
      padding-bottom: 6px;
    }
  }
  img {
    padding: 0;
    margin: 0;
  }
  .social-links {
    margin-top: 40px;
    display: flex;
    a {
      font-size: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(255, 255, 255, 0.1);
      color: #fff;
      line-height: 1;
      margin-right: 8px;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      &:hover {
        background: #18d26e;
      }
    }
  }
}
#header.header-top {
  height: 80px;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.9);
  .social-links {
    display: none;
  }
  h2 {
    display: none;
  }
  h1 {
    margin-right: auto;
    font-size: 36px;
  }
  .container {
    display: flex;
    align-items: center;
  }
}

section {
  overflow: hidden;
  position: absolute;
  width: 100%;
  top: 140px;
  bottom: 100%;
  opacity: 0;
  transition: ease-in-out 0.4s;
  z-index: 2;
  .container {
    background: rgba(0, 0, 0, 0.9);
    padding: 30px;
  }
}
section.section-show {
  top: 100px;
  bottom: auto;
  opacity: 1;
  padding-bottom: 45px;
}
.section-title {
  h2 {
    font-size: 14px;
    font-weight: 500;
    padding: 0;
    line-height: 1px;
    margin: 0 0 20px 0;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #aaaaaa;
    font-family: "Poppins", sans-serif;
    &::after {
      content: "";
      width: 120px;
      height: 1px;
      display: inline-block;
      background: #4ceb95;
      margin: 4px 10px;
    }
  }
  p {
    margin: 0;
    margin: -15px 0 15px 0;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: #fff;
  }
}
.about-me {
  .content {
    h3 {
      font-weight: 700;
      font-size: 26px;
      color: #18d26e;
    }
    ul {
      list-style: none;
      padding: 0;
      li {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
      }
      strong {
        margin-right: 10px;
      }
      i {
        font-size: 16px;
        margin-right: 5px;
        color: #18d26e;
        line-height: 0;
      }
    }
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.counts {
  padding: 70px 0 60px;
  .count-box {
    padding: 30px 30px 25px 30px;
    width: 100%;
    position: relative;
    text-align: center;
    background: rgba(255, 255, 255, 0.08);
    i {
      position: absolute;
      top: -25px;
      left: 50%;
      transform: translateX(-50%);
      font-size: 24px;
      background: rgba(255, 255, 255, 0.1);
      padding: 12px;
      color: #18d26e;
      border-radius: 50px;
      line-height: 0;
    }
    span {
      font-size: 36px;
      display: block;
      font-weight: 600;
      color: #fff;
    }
    p {
      padding: 0;
      margin: 0;
      font-family: "Raleway", sans-serif;
      font-size: 14px;
    }
  }
}
.skills {
  .progress {
    height: 60px;
    display: block;
    background: none;
    border-radius: 0;
    .skill {
      padding: 10px 0;
      margin: 0;
      text-transform: uppercase;
      display: block;
      font-weight: 600;
      font-family: "Poppins", sans-serif;
      color: #fff;
      .val {
        float: right;
        font-style: normal;
      }
    }
  }
  .progress-bar-wrap {
    background: rgba(255, 255, 255, 0.2);
  }
  .progress-bar {
    width: 1px;
    height: 10px;
    transition: 0.9s;
    background-color: #18d26e;
  }
}
.interests {
  .icon-box {
    display: flex;
    align-items: center;
    padding: 20px;
    background: rgba(255, 255, 255, 0.08);
    transition: ease-in-out 0.3s;
    i {
      font-size: 32px;
      padding-right: 10px;
      line-height: 1;
    }
    h3 {
      font-weight: 700;
      margin: 0;
      padding: 0;
      line-height: 1;
      font-size: 16px;
      color: #fff;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.12);
    }
  }
}
.testimonials {
  .testimonials-carousel {
    overflow: hidden;
  }
  .testimonials-slider {
    overflow: hidden;
  }
  .testimonial-item {
    box-sizing: content-box;
    min-height: 320px;
    .testimonial-img {
      width: 90px;
      border-radius: 50%;
      margin: -40px 0 0 40px;
      position: relative;
      z-index: 2;
      border: 6px solid rgba(255, 255, 255, 0.12);
    }
    h3 {
      font-size: 18px;
      font-weight: bold;
      margin: 10px 0 5px 45px;
      color: #fff;
    }
    h4 {
      font-size: 14px;
      color: #999;
      margin: 0 0 0 45px;
    }
    .quote-icon-left {
      color: rgba(255, 255, 255, 0.25);
      font-size: 26px;
      display: inline-block;
      left: -5px;
      position: relative;
    }
    .quote-icon-right {
      color: rgba(255, 255, 255, 0.25);
      font-size: 26px;
      display: inline-block;
      right: -5px;
      position: relative;
      top: 10px;
    }
    p {
      font-style: italic;
      margin: 0 15px 0 15px;
      padding: 20px 20px 60px 20px;
      background: rgba(255, 255, 255, 0.1);
      position: relative;
      border-radius: 6px;
      position: relative;
      z-index: 1;
    }
  }
  .swiper-pagination {
    margin-top: 20px;
    position: relative;
    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      opacity: 1;
      background-color: rgba(255, 255, 255, 0.3);
    }
    .swiper-pagination-bullet-active {
      background-color: #18d26e;
    }
  }
}
.resume {
  .resume-title {
    font-size: 26px;
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #fff;
  }
  .resume-item {
    padding: 0 0 20px 20px;
    margin-top: -2px;
    border-left: 2px solid rgba(255, 255, 255, 0.2);
    position: relative;
    h4 {
      line-height: 18px;
      font-size: 18px;
      font-weight: 600;
      text-transform: uppercase;
      font-family: "Poppins", sans-serif;
      color: #18d26e;
      margin-bottom: 10px;
    }
    h5 {
      font-size: 16px;
      background: rgba(255, 255, 255, 0.15);
      padding: 5px 15px;
      display: inline-block;
      font-weight: 600;
      margin-bottom: 10px;
    }
    ul {
      padding-left: 20px;
      li {
        padding-bottom: 10px;
      }
    }
    &:last-child {
      padding-bottom: 0;
    }
    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      border-radius: 50px;
      left: -9px;
      top: 0;
      background: #18d26e;
      border: 2px solid #18d26e;
    }
  }
}
.services {
  .icon-box {
    text-align: center;
    background: rgba(204, 204, 204, 0.1);
    padding: 80px 20px;
    transition: all ease-in-out 0.3s;
    .icon {
      margin: 0 auto;
      width: 64px;
      height: 64px;
      background: #18d26e;
      border-radius: 5px;
      transition: all 0.3s ease-out 0s;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      transform-style: preserve-3d;
      i {
        color: #fff;
        font-size: 28px;
      }
      &::before {
        position: absolute;
        content: "";
        left: -8px;
        top: -8px;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 0.15);
        border-radius: 5px;
        transition: all 0.3s ease-out 0s;
        transform: translateZ(-1px);
      }
    }
    h4 {
      font-weight: 700;
      margin-bottom: 15px;
      font-size: 24px;
      a {
        color: #fff;
      }
    }
    p {
      line-height: 24px;
      font-size: 14px;
      margin-bottom: 0;
    }
    &:hover {
      background: #18d26e;
      border-color: #18d26e;
      .icon {
        background: #fff;
        i {
          color: #18d26e;
        }
        &::before {
          background: #35e888;
        }
      }
      h4 {
        a {
          color: #fff;
        }
      }
      p {
        color: #fff;
      }
    }
  }
}
.portfolio {
  .portfolio-item {
    margin-bottom: 30px;
  }
  #portfolio-flters {
    padding: 0;
    margin: 0 auto 15px auto;
    list-style: none;
    text-align: center;
    border-radius: 50px;
    padding: 2px 15px;
    li {
      cursor: pointer;
      display: inline-block;
      padding: 8px 16px 10px 16px;
      font-size: 14px;
      font-weight: 600;
      line-height: 1;
      text-transform: uppercase;
      color: #fff;
      background: rgba(255, 255, 255, 0.1);
      margin: 0 3px 10px 3px;
      transition: all 0.3s ease-in-out;
      border-radius: 4px;
      &:hover {
        background: #18d26e;
      }
      &:last-child {
        margin-right: 0;
      }
    }
    li.filter-active {
      background: #18d26e;
    }
  }
  .portfolio-wrap {
    transition: 0.3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    background: rgba(0, 0, 0, 0.6);
    &::before {
      content: "";
      background: rgba(0, 0, 0, 0.6);
      position: absolute;
      left: 30px;
      right: 30px;
      top: 30px;
      bottom: 30px;
      transition: all ease-in-out 0.3s;
      z-index: 2;
      opacity: 0;
    }
    .portfolio-info {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      z-index: 3;
      transition: all ease-in-out 0.3s;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &::before {
        display: block;
        content: "";
        width: 48px;
        height: 48px;
        position: absolute;
        top: 35px;
        left: 35px;
        border-top: 3px solid #fff;
        border-left: 3px solid #fff;
        transition: all 0.5s ease 0s;
        z-index: 9994;
      }
      &::after {
        display: block;
        content: "";
        width: 48px;
        height: 48px;
        position: absolute;
        bottom: 35px;
        right: 35px;
        border-bottom: 3px solid #fff;
        border-right: 3px solid #fff;
        transition: all 0.5s ease 0s;
        z-index: 9994;
      }
      h4 {
        font-size: 20px;
        color: #fff;
        font-weight: 600;
      }
      p {
        color: #ffffff;
        font-size: 14px;
        text-transform: uppercase;
        padding: 0;
        margin: 0;
      }
    }
    .portfolio-links {
      text-align: center;
      z-index: 4;
      a {
        color: #fff;
        margin: 0 2px;
        font-size: 28px;
        display: inline-block;
        transition: 0.3s;
        &:hover {
          color: #63eda3;
        }
      }
    }
    &:hover {
      &::before {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 1;
      }
      .portfolio-info {
        opacity: 1;
        &::before {
          top: 15px;
          left: 15px;
        }
        &::after {
          bottom: 15px;
          right: 15px;
        }
      }
    }
  }
}
.portfolio-details {
  padding-top: 40px;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  .container {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .portfolio-title {
    font-size: 26px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  .portfolio-info {
    padding-top: 45px;
    h3 {
      font-size: 22px;
      font-weight: 400;
      margin-bottom: 20px;
    }
    ul {
      list-style: none;
      padding: 0;
      font-size: 15px;
      li+li {
        margin-top: 10px;
      }
    }
    p {
      font-size: 15px;
      padding: 15px 0 0 0;
    }
  }
  .swiper-pagination {
    margin-top: 20px;
    position: relative;
    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      opacity: 1;
      background-color: rgba(255, 255, 255, 0.3);
    }
    .swiper-pagination-bullet-active {
      background-color: #18d26e;
    }
  }
}
.contact {
  .info-box {
    color: #444444;
    padding: 20px;
    width: 100%;
    background: rgba(255, 255, 255, 0.08);
    i.bx {
      font-size: 24px;
      color: #18d26e;
      border-radius: 50%;
      padding: 14px;
      float: left;
      background: rgba(255, 255, 255, 0.1);
    }
    h3 {
      font-size: 20px;
      color: rgba(255, 255, 255, 0.5);
      font-weight: 700;
      margin: 10px 0 8px 68px;
    }
    p {
      padding: 0;
      color: #fff;
      line-height: 24px;
      font-size: 14px;
      margin: 0 0 0 68px;
    }
    .social-links {
      margin: 5px 0 0 68px;
      display: flex;
      a {
        font-size: 18px;
        display: inline-block;
        color: #fff;
        line-height: 1;
        margin-right: 12px;
        transition: 0.3s;
        &:hover {
          color: #18d26e;
        }
      }
    }
  }
  .php-email-form {
    padding: 30px;
    background: rgba(255, 255, 255, 0.08);
    .error-message {
      display: none;
      background: rgba(255, 255, 255, 0.08);
      background: #ed3c0d;
      text-align: left;
      padding: 15px;
      font-weight: 600;
      br+br {
        margin-top: 25px;
      }
    }
    .sent-message {
      display: none;
      background: rgba(255, 255, 255, 0.08);
      background: #18d26e;
      text-align: center;
      padding: 15px;
      font-weight: 600;
    }
    .loading {
      display: none;
      background: rgba(255, 255, 255, 0.08);
      text-align: center;
      padding: 15px;
      &:before {
        content: "";
        display: inline-block;
        border-radius: 50%;
        width: 24px;
        height: 24px;
        margin: 0 10px -6px 0;
        border: 3px solid #18d26e;
        border-top-color: #eee;
        -webkit-animation: animate-loading 1s linear infinite;
        animation: animate-loading 1s linear infinite;
      }
    }
    input {
      border-radius: 0;
      box-shadow: none;
      font-size: 14px;
      background: rgba(255, 255, 255, 0.08);
      border: 0;
      transition: 0.3s;
      color: #fff;
      padding: 10px 15px;
      &:focus {
        background-color: rgba(255, 255, 255, 0.11);
      }
      &::-moz-placeholder {
        color: rgba(255, 255, 255, 0.3);
        opacity: 1;
      }
      &::placeholder {
        color: rgba(255, 255, 255, 0.3);
        opacity: 1;
      }
    }
    textarea {
      border-radius: 0;
      box-shadow: none;
      font-size: 14px;
      background: rgba(255, 255, 255, 0.08);
      border: 0;
      transition: 0.3s;
      color: #fff;
      padding: 12px 15px;
      &:focus {
        background-color: rgba(255, 255, 255, 0.11);
      }
      &::-moz-placeholder {
        color: rgba(255, 255, 255, 0.3);
        opacity: 1;
      }
      &::placeholder {
        color: rgba(255, 255, 255, 0.3);
        opacity: 1;
      }
    }
    button[type=submit] {
      background: #18d26e;
      border: 0;
      padding: 10px 30px;
      color: #fff;
      transition: 0.4s;
      border-radius: 4px;
      &:hover {
        background: #15bb62;
      }
    }
  }
}
.credits {
  position: fixed;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 15px;
  text-align: right;
  font-size: 13px;
  color: #fff;
  z-index: 999999;
  a {
    color: #18d26e;
    transition: 0.3s;
    &:hover {
      color: #fff;
    }
  }
}
@media (min-width: 1024px) {
  body {
    &::before {
      background-attachment: fixed;
    }
  }
}
@media (max-width: 992px) {
  #header {
    h1 {
      font-size: 36px;
    }
    h2 {
      font-size: 20px;
      line-height: 30px;
    }
    .social-links {
      margin-top: 15px;
    }
    .container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  .portfolio-details {
    .portfolio-info {
      padding-top: 20px;
    }
  }
  .credits {
    text-align: center;
    padding: 10px;
    background: rgba(0, 0, 0, 0.8);
  }
}
@media (max-width: 768px) {
  #header.header-top {
    height: 60px;
    h1 {
      font-size: 26px;
    }
  }
  section {
    top: 120px;
  }
  section.section-show {
    top: 80px;
  }
}
@media (max-width: 991px) {
  .mobile-nav-toggle {
    display: block;
  }
  .navbar {
    ul {
      display: none;
    }
  }
}

#main {
  height: calc(100vh + 80px);

  @include screen-less('mobile') {
    height: 1138px;
  }
}